@font-face {
  font-family: "PressStart2P";
  src: url(../PressStart2P-Regular.ttf) format("truetype");
}

body {
  margin: 0;
  background: linear-gradient(0deg, rgba(90, 169, 206, 1) 0%, rgba(145, 186, 185, 1) 100%);
}

#stats {
  position: absolute;
  top: 0;
  right: 0;
}

#container {
  width: 100%;
  /* background-color: #2a2a2a; */
  background: linear-gradient(0deg, rgba(90, 169, 206, 1) 0%, rgba(145, 186, 185, 1) 100%);
  display: none;

  & > canvas {
    width: 100%;
  }
}

#minimap {
  position: absolute;
  top: 10px;
  left: 10px;
  width: 200px;
  height: 200px;
  clip-path: circle(50%);
  background-color: #00297582;
  background-color: rgba(0, 41, 117, 0.52);

  & > canvas {
    height: 100%;
    width: 100%;
  }
}

.minimap-border {
  border: 2px solid #1a42af;
  border-radius: 100px;
  z-index: 999;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

#cover {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  pointer-events: none;

  & > div {
    position: relative;
    width: 400px;
    margin: 0px auto;
    padding-top: 40vh;
  }

  img {
    width: 100%;
  }

  .cover-text {
    text-align: center;
    font-family: "PressStart2P";
    color: white;
    display: none;
  }
}

.loader {
  display: block;
  border-radius: 50%;
  border: 2px solid rgba(255, 255, 255, 0.8);
  width: 50px;
  height: 50px;
  margin: 0 auto;
  border-right-color: lightblue;
  // border-image-source: linear-gradient(to left, #ebf1f6 0%,#abd3ee 50%,#89c3eb 51%,#d5ebfb 100%);
  animation: loader-anim 2s ease-in-out forwards infinite;
}

#debug {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100px;
  font-family: consolas;
  font-size: 6pt;
  background-color: #222;
  color: #eee;
  overflow-y: scroll;
}

#debug p {
  line-height: 6px;
}

@keyframes loader-anim {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(359deg);
  }
}
